import { Component } from '@angular/core';
import { LoadingController, NavParams, ViewController } from "ionic-angular";
import { ApiService } from "../../services/api.service";

@Component({
  selector: 'match.details',
  templateUrl: 'match.details.html'
})

export class MatchDetails {

  public matchData: any;
  public fullMatchInfo: any;
  public bgChampImg: string;
  public teamId: any;
  public matchEvents:any = [];

  constructor(private navParams: NavParams,
              private viewController: ViewController,
              private api: ApiService,
              private loadingCtrl: LoadingController) {

    let loading = this.loadingCtrl.create({
      content: 'Загрузка данных...'
    });
    loading.present();
    this.teamId = 100;
    this.matchData = navParams.get('details');
    this.bgChampImg = `http://ddragon.leagueoflegends.com/cdn/img/champion/loading/${this.matchData.champKey}_0.jpg`;
    this.api.getMatchInfo(this.matchData.gameId).subscribe(res => {
      this.matchData.timePlayedString = `${Math.ceil(res.gameDuration / 60)} мин. ${res.gameDuration % 60} сек.`;
      let self = this;
      res.participants.forEach(function (item:any) {
        if (item.championId === self.matchData.champion) {
          self.matchData.stats = item.stats;
          self.matchData.participantId = item.participantId;
        }
      });
      console.log(this.matchData);
      this.fullMatchInfo = res;
      this.fullMatchInfo.participantIdentities.forEach(function (item,index) {
        self.fullMatchInfo.participants[index].summonerName = item.player ? item.player.summonerName : null;
      });
      this.fullMatchInfo.participants.forEach(function (item, index) {
        self.api.getChampById(item.championId).subscribe(res => {
          item.champKey = res.key;
        });
      });
      this.api.getMatchTimeline(this.matchData.gameId).subscribe(res => {
        console.log(res);
        res.frames.forEach(function (item:any) {
          if (item.events.length > 0) {
            item.events.forEach(function (event) {
              if (event.participantId === self.matchData.participantId || event.killerId === self.matchData.participantId || event.victimId === self.matchData.participantId) {
                let action = {
                  type: event.type,
                  item: event.itemId || null,
                  victim: event.victimId || null,
                  killer: event.killerId || null,
                  timestampString: `${Math.ceil((event.timestamp/1000) / 60)} мин. ${((event.timestamp/1000) % 60).toFixed(0)} сек.`,
                };
                self.matchEvents.push(action);
                self.getChampInfoByParticipantId();
              }
            });
          }
        });
        console.log(this.matchEvents);
      });
      loading.dismiss();
    });
  }

  closeDetails() {
    this.viewController.dismiss();
  }

  getChampInfoByParticipantId() {
    let self = this;
    let imgs: any = [];

    this.matchEvents.forEach(function (event) {
      if (event.type === 'CHAMPION_KILL') {
        self.fullMatchInfo.participants.forEach(function (item) {
          if (item.participantId === event.killer && item.participantId !== self.matchData.participantId) {
            event.actionInfo = {
              key: item.champKey,
              type: 'Смерть'
            };
          } else if (item.participantId === event.victim && item.participantId !== self.matchData.participantId) {
            event.actionInfo = {
              key: item.champKey,
              type: 'Убийство'
            };
          }
        });
      }
    })

  }

}



